.App {
  text-align: center;
}

.App-swirl {
  /* animation: App-logo-spin infinite 20s linear;  */
  height: 60vmin;
  opacity: 0.3;
  
  margin: auto;
  position: absolute;
  /* display: flex; 
  align-items: center;
  justify-content: center; 
  text-align: center;   */
  z-index:0;
}
.App-logo {
  /* animation: App-logo-spin infinite 20s linear; */
  height: 40vmin;
  z-index:1;
}


.App-header {
  background-color: #282c34;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
